
import VueQArt from 'vue-qart'
import QArt from 'qartjs'
// v-if="!!goods_data_list.new_assemblies.goods_id"
import api from '../api/index.js'
// import {mapState,mapActions} from 'vuex'

  import Swiper from 'vux/src/components/swiper/swiper.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'

  export default {
    name: 'goods',
    data: function () {
      return {
        config: {
          value: window.location.href,
          imagePath: '/static/slice/girl_avatar.jpg',
          filter: 'color'
        },
        index: 0,
        goods_id: 0,
        item_index: 0,
        page_goods_data: {},
        current_page: 0,
        temp_data:{},
        collapse1:true,
        collapse2:true,
        collapse3:true,
        collapse4:true,
        collapse5:true,
         list: [],
        options: {
          getThumbBoundsFn (index) {
            // find thumbnail element
            let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]
            // get window scroll Y
            let pageYScroll = window.pageYOffset || document.documentElement.scrollTop
            // optionally get horizontal scroll
            // get position of element relative to viewport
            let rect = thumbnail.getBoundingClientRect()
            // w = width
            return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}
            // Good guide on how to get element coordinates:
            // http://javascript.info/tutorial/coordinates
         }
        },
        parms_table:{
          assemblies:{},
          electrics:{},
          goods_ports:{},
          mechanics:{},
          standardfits:{}
        },
        goods_data_list: {
          name: '',
          content: '',
          price: '',
          mktprice: '',
          image_attach:[],
          new_assemblies:{},
          new_electrics:{},
          new_goods_ports:{},
          new_standardfits:{},
        },
        from: 0,
        last_page: 0,
        per_page: 0,
        to: 0,
        total: 0,
      }
    },
   
    methods: {
      show:function(index){
        console.log("展示");
        console.log(this.list);
        this.$refs.previewer.show(index);
      },
      collapse:function(index){
        this["collapse"+index]=!this["collapse"+index];
      },
      // ...mapActions(['GETGOODSLIST']),
      init_goods_page: function (query) {
         var self=this;
          // this.GETGOODSLIST({relations: ["image_attach", "images"], parameters:{goods_id:39}});
          api.getGoodsData({relations: ["image_attach", "images","mechanics","goods_ports","assemblies","standardfits","electrics"], parameters:query}).then((res)=>{
            // self.goods_data_list=res.data.data[0];
            self.handle_goods_data(res.data.data[0]);
            console.log(res);
          });
      },
      handle_goods_data:function(data){
        console.log(data);
        var self=this;
        for(var key in data){
          if(key=="electrics"||key=="assemblies"||key=="goods_ports"||key=="standardfits"){
            var new_obj={};
            for(var i=0;i<data[key].length;i++){
              //遍历 key 值,相同做数据合并
              for(var k in data[key][i]){
                if(k=="created_at"||k=="id"||k=="updated_at"||k=="type"){
                  continue;
                }
                if(!!data[key][i][k]){
                  // if(!!new_obj[k]){
                  //   if(new_obj[k]==data[key][i][k]){
                  //     continue;
                  //   }
                  //   new_obj[k]=new_obj[k]+'  '+data[key][i][k];
                  // }else{
                  //   new_obj[k]=data[key][i][k];
                  // } 
                  if(!new_obj[k]){
                    new_obj[k]=[];
                  } 
                   new_obj[k].push(data[key][i][k]);
                  // new_obj[i]=data[key][i][k];
                }
              }
            }
            console.log(new_obj);
            data['new_'+key]=new_obj;
            console.log(".............");
            self.goods_data_list=data;
          }
        }
        // console.log(data);
        console.log(self.goods_data_list);
        setTimeout(function(){
          self.show_previewer();
        },2000)
      },  
      get_parms_data:function(){
        var self=this;
        api.get_trans_params_table({relations: ['mechanics','goods_ports','assemblies','standardfits','electrics',]}).then((res)=>{
          self.parms_table=res.data;
        });
      },
      show_previewer:function(){
        var self=this;
        console.log("________________________");
        console.log($(".goods-desc img"));
        self.list=[];
        $(".goods-desc img").addClass("previewer-demo-img");
        $.each($(".goods-desc img"),function(i,n){
          var obj={
            src:$(this).attr("src"),
            w:($(this).width()+8)*2,
            h:$(this).height()*2
             
          };
          self.list.push(obj);
          // $(this).addClass("previewer-demo-img");
          $(this).on("click",function(){
            console.log(i);
            self.show(i);
          })
        })
        console.log(self.list);
       
      }
    },
    created: function () {
      var query = this.$route.query;
      console.log("create good page");
      this.goods_id = query.goods_id;
      this.item_index = query.item_index;
      this.init_goods_page(query);
      this.get_parms_data();
    },
    components: {
      Swiper,
      Flexbox,
      FlexboxItem,
      XButton,
      Tab,
      TabItem,
      SwiperItem,
      Group,
      Cell,
      VueQArt,
      Previewer
    }
  }




