
// <keep-alive> </keep-alive>
import * as config from './config/config.js'
import api from './api'
  import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Search from 'vux/src/components/search/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


  export default {
    name: 'app',
    data: function () {
      return {
        logo_image:config.app_config.logo_image,
        email:'',
        password:'',
        popup_login: false,
        page_name: '',
        search_input: '',
        intercept_closeable:true,
        // title:'用户注册',
        header_conf: {
          title: '',
          is_show: true,
          show_back: false
        },
        navbar: {
          
        }
      }
    },
    computed:{
      
    },
    methods: {
      submit_login:function(){
          var self=this;
          if(this.email==""){
            this.$vux.toast.show({
              text:'<span class="font-normal">邮箱不能为空</span>',
              type:"warn"
            })
            return;
          }else if(this.password==""){
            this.$vux.toast.show({
              text:'<span class="font-normal">请输入密码</span>',
              type:"warn"
            })
            return;
          };
          api.user_login({
            email:self.email,
            password:self.password
          }).then(res=>{
            console.log(res);
            var res_data=res.data;
            if(res_data.res){
              self.$vux.toast.show({
                text:'<span class="font-normal">'+res_data.req+'</span>',
                type:'success'
              });
              self.save_token(res_data.data);
              setTimeout(function(){
                self.popup_login=false;
              },500)
            }else{
                self.$vux.toast.show({
                text:'<span class="font-normal">'+res_data.req+'</span>',
                type:'warn'
              })
            }
          })
      },
      fetch_data: function () {
        this.popup_login = false;
        this.page_name = this.$route.name;
        this.xheader_handler(this.$route);
        //处理 navbar 的 active
      },
      init_app:function(){
        var self=this;
        //调用header处理
        this.page_name = this.$route.name;
        var header_conf = this.handler_xheader(this.$route.name);
        this.header_conf = header_conf;
        this.navbar=config.app_config.navbar;

         //3s后启用登陆拦截
        setTimeout(function(){
            self.popup_login=self.$store.state.popuplogin.popup_login;
            self.intercept_closeable=config.app_config.intercept_closeable;
            console.log(self.popup_login);
            console.log("登陆拦截");
         },config.app_config.intercept_time)
      },
      popup: function () {
        this.popup_login = !this.popup_login;
      },
      xheader_handler: function (route) {
        var header_conf = this.handler_xheader(route.name);
        this.header_conf = header_conf;
      }
    },
    created: function () {
      this.init_app();
    },
    watch: {
      $route: 'fetch_data'
    },
    components: {
      XHeader,
      Tabbar,
      TabbarItem,
      Search,
      Popup,
      Group,
      XInput,
      XButton
    }
  }




